<template>
  <div>
    <am-nav-bar title="am-list-input"></am-nav-bar>
    <scroller class="scroller">
      <div>
        <am-list header="am-list-input">
          <am-list-input v-model="inputValue" title="" @change="test" placeholder=""></am-list-input>
          <am-list-input v-model="inputValue" title="" placeholder=""></am-list-input>
          <am-list-input v-model="inputValue" title="" placeholder="" align="right"></am-list-input>
          <am-list-input
            type="number"
            title=""
            v-model="inputValue2"
            :title-number="3"
            :autofocus="true"
            placeholder="autofocus"
          ></am-list-input>
          <am-list-input
            title=""
            v-model="inputValue"
            :editable="false"
            placeholder=""
          >
          </am-list-input>
          <am-list-input
            title=""
            v-model="inputValue"
            :disabled="true"
            placeholder=""
          >
          </am-list-input>
          <am-list-input
            v-model="inputValue"
            title=""
            :title-number="3"
            :disabled="inputDisbled"
            placeholder=""
          >
            <am-switch v-model="inputDisbled" slot="extra"></am-switch>
          </am-list-input>
          <am-list-input
            v-model="inputValue"
            title=""
            :title-number="3"
            :singleline="false"
            :lines="5"
            :disabled="inputDisbled"
            placeholder=""
          >
          </am-list-input>
        </am-list>
      </div>
    </scroller>
  </div>
</template>

<script>
import { AmNavBar, AmList, AmListItem, AmListInput, AmListRadio, AmButton, AmSwitch } from '../../index'

export default {
  components: { AmNavBar, AmList, AmListItem, AmListRadio, AmListInput, AmButton, AmSwitch },
  data () {
    return {
      inputValue: '',
      inputValue2: '',
      inputDisbled: false
    }
  },
  methods: {
    test (event) {
      console.log(event)
    }
  }
}
</script>



